import styled from "styled-components";

import { colors, spacingMap } from "../../styleguide";
import Typography from "../../Typography/Typography";

export interface StyledLocalNavigationItemProps {
	isActive?: boolean
	isParent?: boolean
	isDisabled?: boolean
}

export const StyledLocalNavigationItem = styled(Typography)<StyledLocalNavigationItemProps>`
	display: flex;
	box-sizing: border-box;
	margin: 0;
	opacity: 0.66;
	height: 36px;
	align-items: center;
	padding: ${spacingMap.xs};
	&:hover{
		opacity: 1;
	}
	border: solid 1px transparent;
	border-radius: 8px;
	gap: ${spacingMap.xs};

	${({ isActive }) => isActive && `
		opacity: 1;
		color: ${colors.midBlue};
		background-color: ${colors.white}80;
		border: solid 1px ${colors.white};
		border-bottom: solid 1px ${colors.white};
	`}

	${({ isParent }) => isParent && `
		border-bottom: 0;
		border-radius: 8px 8px 0 0;
	`}

	${({ isDisabled }) => isDisabled && `
		color: ${colors.black};
		cursor: not-allowed;
		opacity: .33;
		&:hover{
			opacity: .33;
		};
	`}
`;

export const RightBar = styled.span<StyledLocalNavigationItemProps>`
	position: absolute;
	width: 4px;
	height: 37px;
	border-radius: 2px 0 0 2px;
	right: -8px;
	${({ isActive }) => isActive && ({
		opacity: 1,
		backgroundColor: colors.midBlue,
		color: colors.midBlue,
	})}
	&:focus, &:focus-visible {
		outline: none;
	}
`;
